<template>
    <section class="hero is-medium has-text-centered" v-once>
        <div class="hero-body">
            <div class="container">
                <div class="columns">
                    <div class="column">
                        <div class="tile is-ancestor">
                            <div class="tile is-child has-text-centered">
                                <transition appear name="slide-fade">
                                    <div class="content" v-show="show">
                                        <img src="../assets/logo.png">

                                        <h1 class="title is-1">
                                            Matchr
                                        </h1>

                                        <h3 class="subtitle">
                                            Matching colors made easy.
                                        </h3>
                                        <p>
                                            Move the mouse around. Scroll up or down to change the hue. <br> Double click to
                                            save.
                                        </p>

                                        <button class="button is-outlined" @click="dismiss">
                                            Got it!
                                        </button>
                                    </div>
                                </transition>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script type="text/babel">
    export default {
        data() {
            return {
                show: true
            }
        },

        methods: {
            dismiss() {
                this.show = false
            }
        }
    }
</script>


